.search {
    display: flex;
    align-items: center;
    min-width: 340px;
    height: 82px;
    margin: 0 auto;
    margin-bottom: 50px;
    position: sticky;
    background:  linear-gradient(90deg,#17dba3,#0b7373);
    top: -22px;
    &__input {
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 310px;
        max-height: 30px;
        font-size: 16px;
        padding: 12px 8px;
        background: #ffffff;
        border: 0;
        outline: 0;
        text-indent: 8px;
        &:focus {
            border: 0;
            outline: 0; } } }

